import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, inject, OnInit } from '@angular/core';
import { RouterLink } from "@angular/router";
import { environment } from '../../../../environments/environment';
import { CommonModule } from '@angular/common';
import { TimeAgoPipe } from "../../../common/time-ago.pipe";
import { ToastService } from '../../../common/toast.service';
import { AuthService } from '../../../services/auth.service';

@Component({
  selector: 'app-ias-community',
  standalone: true,
  imports: [RouterLink, CommonModule, TimeAgoPipe],
  templateUrl: './ias-community.component.html',
  styleUrl: './ias-community.component.scss'
})
export class IasCommunityComponent implements OnInit {
  auth = inject(AuthService);
  count = 30;
  animate = false;
  posts: any;
  categories: any[] = [];
  apiCategories: any[] = [];
  selectedCategory = 0;
  toastMessage: string = '';
s3Url = environment.s3Url;
  constructor(private http: HttpClient, private toastService: ToastService){}
ngOnInit(): void {
  this.loadtags();
  this.auth.checkAuth();
  this.community();
}
toggleLike(post: any) {
  if (!post) return;
  const token = localStorage.getItem('authToken');
  if (!token) {
    this.toastService.showToast('Please login to like the posts.', 'success');
    return; // stop here
  }

  // trigger animation
  post.animate = true;

  // optimistic UI update
  const prevLiked = post.isLiked;
  post.isLiked = !prevLiked;
  post.likes += post.isLiked ? 1 : -1;

  // call API
  this.http.post<any>(`${environment.apiUrl}/communities/likes`, { communityId: post.id })
    .subscribe({
      next: (res) => {
        console.log("Like API success:", res);
      },
      error: (err) => {
        console.error("Error in Like post:", err);
        // revert if failed
        post.isLiked = prevLiked;
        post.likes += post.isLiked ? 1 : -1;
      }
    });

  // reset animate flag after animation
  setTimeout(() => { post.animate = false; }, 300);
}
loadtags() {

  this.http.get<any>(`${environment.apiUrl}/post-tags?limit=0`)
    .subscribe({
      next: (res) => {
        console.log(res);
        this.categories = res.data.data;
        this.apiCategories = [{ id: 0, name: 'All' }, ...this.categories];
        console.log(this.categories);
      },
      error: (err) => {
        console.error('Error fetching Events:', err);
      }
    });
}
community() {
  if (this.auth.isLoggedIn() === true) {
this.communityposts(true);
  }
  else if(this.auth.isLoggedIn() === false){
    this.communityposts(false);
  }
}
communityposts(forUser: boolean) {
  let URL;
  if (forUser) {
    URL =`${environment.apiUrl}/communities/users?populate=createdBy&limit=50`
  }
  else {
    URL =`${environment.apiUrl}/communities?populate=createdBy&limit=50`
  }
 
  this.http.get<any>(URL)
    .subscribe({
      next: (res) => {
        // console.log(res);
        this.posts = res.data.data;
        // console.log("postsssss", this.posts);
      },
      error: (err) => {
        console.error('Error fetching Events:', err);
      }
    });
}
selectCategory(cat: number) {
  this.selectedCategory = cat;
  console.log("Selected:", cat);
  // here you can trigger API/filter logic
  if (cat === 0) {
    this.community();
  } else {
    this.http.get<any>(`${environment.apiUrl}/communities?populate=createdBy&postTagId=${cat}`)
      .subscribe({
        next: (res) => {
          // console.log(res);
          this.posts = res.data.data;
          console.log("postsssss", this.posts);
        },
        error: (err) => {
          console.error('Error fetching Events:', err);
        }
      });
  }
}
copyPostLink(postId: string) {
  const url = `${window.location.origin}/post?id=${postId}`;

  navigator.clipboard.writeText(url).then(() => {
    this.toastService.showToast('Link copied to clipboard ✅', 'success');
  }).catch(err => {
    console.error('Failed to copy link: ', err);
  });
}

}
